<script>
	export default {
		data: () => ({
			showQrCode: false,
			scrollPercent: 0,
			showTopButton: false,
			activePointIndex: 0,
			totalHeight:0,
			points: [
			  { percent: 0, title: "banner" },
			  { percent: 20, title: "about" },
			  { percent: 50, title: "architechtuew" },
			  { percent: 70, title: "roadmap" },
			  { percent: 96, title: "footer" }
			],
			toastShow:false,
			toastText:''
		}),
		computed:{
			currentPointTitle() {
			  return this.points[this.activePointIndex].title;
			},
			isGoTop(){
				return this.scrollPercent > 50;
			},
			leftSeat(){
				return this.$appConfig.defaultLeftSeat;
			},
			walletAddress(){
				return this.$appConfig.defaultWalletAddress;
			},
			leftPercent(){
				const percent = (this.$appConfig.defaultLeftSeat/this.$appConfig.defaultTotalSeat * 100 )+ "%";
				console.log(percent);
				return percent;
			}
		},
		mounted() {
			window.addEventListener('scroll', this.handleScroll);
		},
		beforeDestroy() {
			window.removeEventListener('scroll', this.handleScroll);
		},
		methods: {
			async copyAddress(){
				await navigator.clipboard.writeText(this.walletAddress);
				this.toastText = this.$t('common.copySuccess');
				this.toastShow = true;
				setTimeout(()=>{
					this.toastShow = false;
				}, 1000)
			},
			triggerQrCodeShow(){
				this.showQrCode = !this.showQrCode;
			},
			throttle(fn, delay) {
				let lastTime = 0;
				return function() {
					const now = new Date().getTime();
					if (now - lastTime >= delay) {
						fn.apply(this, arguments);
						lastTime = now;
					}
				};
			},
			handleScroll() {
				const scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
				const totalHeight = document.documentElement.scrollHeight - document.documentElement.clientHeight;
				this.totalHeight = totalHeight;
			
				this.scrollPercent = totalHeight > 0 ? (scrollTop / totalHeight) * 100 : 0;

				this.showTopButton = this.scrollPercent > 30;

				let minDistance = Infinity;
				let activeIndex = 0;

				this.points.forEach((point, index) => {
					const distance = Math.abs(this.scrollPercent - point.percent);
					if (distance < minDistance) {
						minDistance = distance;
						activeIndex = index;
					}
				});

				this.activePointIndex = activeIndex;
			},
			goJump(){
				console.log(this.isGoTop)
				if(this.isGoTop){
					window.scrollTo({
						top: 0,
						behavior: 'smooth'
					});
				}else{
					console.log(this.totalHeight)
					if( this.tableHeight == 0){
						this.tableHeight = document.documentElement.scrollHeight - document.documentElement.clientHeight;
					}
					console.log(this.totalHeight)
					window.scrollTo({
						top: this.totalHeight,
						behavior: 'smooth'
					});
				}
			}
		}
	};
</script>

